/* src/components/Home.css */
.home-container {
    text-align: center;
    padding: 20px;
  }
  
  .home-header {
    background-color: #282c34;
    padding: 20px;
    color: white;
  }
  
  .home-header h1 {
    font-size: 2.5rem;
    margin: 0;
  }
  
  .home-header p {
    font-size: 1.2rem;
    margin: 10px 0 0;
  }
  
  .home-content {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 20px 0;
  }
  
  .home-card {
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 20px;
    margin: 10px;
    flex-basis: 30%;
  }
  
  .home-card h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .home-card p {
    font-size: 1rem;
    color: #333;
  }
  